@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-color: #7bdaf3;
	font-family: 'Poppins', sans-serif;
	padding-top: 3rem;
}

.wrap {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.add {
	position: fixed;
	top: 1rem;
	right: 1rem;
	background-color: #9ec862;
	color: #fff;
	border: none;
	border-radius: 3px;
	padding: 0.5rem 1rem;
	cursor: pointer;
}

.add:active {
	-webkit-transform: scale(0.98);
	        transform: scale(0.98);
}

.note {
	background-color: #fff;
	box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.1);
	margin: 30px 20px;
	height: 400px;
	width: 400px;
	overflow-y: scroll;
}

.note .tools {
	background-color: #9ec862;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	padding: 0.5rem;
}

.note .tools button {
	background-color: transparent;
	border: none;
	color: #fff;
	cursor: pointer;
	font-size: 1rem;
	margin-left: 0.5rem;
}

.note textarea {
	outline: none;
	font-family: inherit;
	font-size: 1.2rem;
	border: none;
	height: 400px;
	width: 100%;
	padding: 20px;
}

.main {
	padding: 20px;
}

.hidden {
	display: none;
}

